import React, { useState } from "react";
import { useDispatch } from "react-redux";
import {
  addTextAnswer,
  deleteTextAnswer,
} from "../../../redux/reducer/DailyQsReducer";
import { useForm } from "react-hook-form";
import classes from "./CalendarSurvey.module.scss";

const CalendarSurvey = ({
  data,
  slideIndex,
  maxIndex,
  slidePosition,
  postToggle,
  postDaily,
}) => {
  let prevIndex;
  let nextIndex;
  const { register, handleSubmit } = useForm();

  const dispatch = useDispatch();
  const [dis, setDis] = useState(false);
  const [status, setStatus] = useState(false);

  if (slideIndex === 0) {
    prevIndex = maxIndex;
    nextIndex = 1;
  } else if (slideIndex === maxIndex) {
    prevIndex = 1;
    nextIndex = 0;
  } else {
    prevIndex = slideIndex - 1;
    nextIndex = slideIndex + 1;
  }

  const onSubmit = (e) => {
    if (!dis) {
      dispatch(addTextAnswer({ index: data.index, answer: e.dailyQs, status }));
    } else {
      dispatch(deleteTextAnswer(data.index));
    }
    setDis(!dis);
  };

  return (
    <>
      <div
        className={
          slidePosition === "prev" && nextIndex === data.index
            ? "slide prev"
            : slideIndex === data.index
            ? "slide now"
            : slidePosition === "next" && prevIndex === data.index
            ? "slide next"
            : "slide"
        }
      >
        <form onSubmit={handleSubmit(onSubmit)}>
          <span className={classes.span}>{data.qs}</span>

          <textarea
            disabled={dis}
            className={classes.survey__textarea}
            rows="1"
            spellCheck={false}
            {...register("dailyQs")}
          />
          <div
            style={{
              top: "14rem",
              width: "fit-content",
              display: "inline-block",
              position: "absolute",
              left: "50%",
              transform: "translate(-50%,-50%)",
            }}
            className={
              !status ? classes.survey__btn : classes.survey__btn__active
            }
            onClick={() => setStatus(!status)}
          >
            {!status ? "" : ""}
          </div>

          <button
            className={dis ? classes.survey__btn__active : classes.survey__btn}
            type="submit"
          >
            {dis ? "" : ""}
          </button>
          {postToggle && (
            <button
              className={classes.survey__btn__post}
              style={{ top: "20rem" }}
              onClick={postDaily}
            >
              
            </button>
          )}
        </form>
      </div>
    </>
  );
};

export default CalendarSurvey;
